import React, { useEffect, useRef } from "react";
import firebaseui from "firebaseui"; // Correct import for older versions
import { auth } from "@/firebase/config"; // Your Firebase initialization

interface FirebaseUIWidgetProps {
  uiConfig: firebaseui.auth.Config;
}

const FirebaseUIWidget: React.FC<FirebaseUIWidgetProps> = ({ uiConfig }) => {
  const firebaseUiContainer = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (firebaseUiContainer.current) {
      try {
        const ui = new firebaseui.auth.AuthUI(auth);
        ui.start(firebaseUiContainer.current, uiConfig);

        return () => {
          ui.delete();
        };
      } catch (error) {
        console.error("FirebaseUI Error:", error);
      }
    }
  }, [uiConfig]);

  return <div ref={firebaseUiContainer} />;
};

export default FirebaseUIWidget;
